import {
    Link
} from "react-router-dom";

const DashboardVehicleDetail = ({ vehicleList, deleteVehicle }) => {

    const formatVehicleType = (data) => {
        //console.log(data)
        if (data.vehicleType === "Sport Utility Vehicle (SUV)/Multi-Purpose Vehicle (MPV)") {
            return "Sport Utility Vehicle (SUV)"
        }else{
            return data.vehicleType
        }
    }

    return (
        <div class="w-100vw space-y-6 ">

            {vehicleList.map(data => {
                return (
                    <div class="border-2 bg-gray-400 rounded-lg" key={data.id}>

                        <div class="flex justify-between border-b-2 border-black pb-3">
                            <h1 class="text-4xl pb-2 pt-2 pl-2 pr-2 mr-28">{formatVehicleType(data)}</h1>
                            <div class="pt-4 space-x-4 pr-2" >
                                <Link to={`/vehicle-status/${data.id}`} class="bg-yellow-600 p-1.5 active:bg-yellow-600 hover:bg-blue-500">
                                    Track Maintenance
                                </Link>
                                <button class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500" onClick={deleteVehicle} id={data.id}>Delete</button>
                            </div>
                        </div>

                        <div class="flex justify-center mt-5">
                            <div class="grid grid-cols-3 w-3/4 text-center">
                                <p class="font-black text-3xl">YEAR</p>
                                <p class="font-black text-3xl">MAKE</p>
                                <p class="font-black text-3xl">MODEL</p>
                                <p class="text-xl">{data.modelYear}</p>
                                <p class="text-xl">{data.make}</p>
                                <p class="text-xl">{data.model}</p>
                            </div>
                        </div>

                        <div class="flex justify-center mt-5 pb-4">
                            <div class="grid grid-cols-2 w-3/4 text-center">
                                <p class="font-black text-2xl">COLOR</p>
                                <p class="font-black text-2xl">PLATE #</p>
                                <p class="text-xl">{data.color}</p>
                                <p class="text-xl">{data.plateNumber}</p>
                            </div>
                        </div>
                    </div>
                )
            })}

        </div>
    )
}

export default DashboardVehicleDetail;