import { useHistory } from "react-router-dom";
import vehicle from "../services/vehicle";

const VehicleConfirmation = ({ vehicleType, primaryInfo, make, model, modelYear }) => {
    const history = useHistory();
    let body;

    if (vehicleType !== "" && vehicleType === "Sport Utility Vehicle (SUV)/Multi-Purpose Vehicle (MPV)") {
        body = "Sport Utility Vehicle (SUV)"
    } else {
        body = vehicleType
    }

    const addVehicle = () => {

        let vehicleInfo = {
            make: make,
            model: model,
            modelYear: modelYear,
            vehicleType: vehicleType
        }

        let finalVehObj = {
            ...vehicleInfo,
            ...primaryInfo
        }

        vehicle.addVehicle(finalVehObj)
        history.push("/user-dashboard");
    }


    if (make === "" || model === "") {
        return (
            null
        );
    }

    if (make !== "" && model !== "") {
        return (
            <div class="container w-1/2 border-2 mx-auto bg-white mb-6 p-4 rounded-lg">
                <div class="bg-gray-100 p-4">
                    <div class="text-center">
                        <h1 class="text-xl">Your Vehicle</h1>
                        <p>If the results look incorrect, please double check your VIN above!</p>
                    </div>
                    <div class="flex justify-center">
                        <div class="grid grid-cols-4 w-auto border-2 text-center m-2 p-2">
                            <p class="text-2xl">Year</p>
                            <p class="text-2xl">Make</p>
                            <p class="text-2xl">Model</p>
                            <p class="text-2xl">Vehicle Type</p>
                            <p class="text-xl">{modelYear}</p>
                            <p class="text-xl">{make}</p>
                            <p class="text-xl">{model}</p>
                            <p class="text-xl">{body}</p>
                        </div>
                    </div>
                    <div class="flex justify-center">
                        <button class="bg-gray-200 p-3 hover:bg-gray-400 active:bg-white" onClick={addVehicle}>Enter Vehicle</button>
                    </div>
                </div>
            </div>
        );
    }
}

export default VehicleConfirmation;