import React, { useState, useEffect } from 'react';
import vehicle from '../services/vehicle';
import DashboardVehicleDetail from '../components/DashboardVehicleDetail';
import DashboardQuickStats from '../components/DashboardQuickStats';

const Dashboard = () => {
    const [vehicleList, setVehicleList] = useState([]);
    const [totalKilometers, setTotalKilometers] = useState("");
    const [totalVehicles, setTotalVehicles] = useState("");

    useEffect(() => {
        vehicle
            .getAllVehicles()
            .then(data => {
                //console.log(data)
                let totalKM = 0;
                for (let i = 0; i < data.length; i++) {
                    let individual = data[i].mileageInformation;
                    totalKM += Number(individual[individual.length - 1].lastMileage);
                }
                setTotalKilometers(totalKM);
                setVehicleList(data);
                setTotalVehicles(data.length);
            })
    }, []); 

    const deleteVehicle = (e) => {
        //console.log(e.target.id)
        vehicle
            .deleteVehicle(e.target.id);

        const updateList = vehicleList.filter(p => p.id !== e.target.id);
        //console.log(updateList)
        setVehicleList(updateList);
    }


    return (
        <div class="flex justify-around text-white mt-10 ">
            <DashboardQuickStats totalKilometers={totalKilometers} totalVehicles={totalVehicles} />
            <DashboardVehicleDetail vehicleList={vehicleList} deleteVehicle={deleteVehicle} />
        </div>

    )
}

export default Dashboard