import React, { useState } from 'react';
import moment from 'moment';
import VehicleConfirmation from '../components/VehicleConfirmation';
import VehicleDetailForm from '../components/VehicleDetailForm';
import vehicle from '../services/vehicle';
import { useHistory } from "react-router-dom";

const VehicleStatus = () => {
    const [vin, setVin] = useState("");
    const [plateNumber, setPlateNumber] = useState("");
    const [date, setDate] = useState("");
    const [color, setColor] = useState("");
    const [vehicleType, setVehicleType] = useState("")
    const [primaryInfo, setPrimaryInfo] = useState({});
    const [make, setMake] = useState("");
    const [model, setModel] = useState("");
    const [modelYear, setModelYear] = useState("");
    const history = useHistory();



    const handleVin = (e) => {
        //console.log(e.target.value)
        setVin(e.target.value);
    }

    const handlePlateNum = (e) => {
        setPlateNumber(e.target.value.toUpperCase());
    }

    const handleInsuranceDate = (e) => {
        setDate(e.target.value);
    }

    const handleColor = (e) => {
        setColor(e.target.value);
    }

    const clearFields = () => {
        setVin("")
        setPlateNumber("")
        setDate("")
    }

    const enterVehicleDetail = (e) => {
        e.preventDefault();

        vehicle
            .getAllVehicles()
            .then(vinArr => {
                let empty = false;
                let contains = false;

                for (let i = 0; i < vinArr.length; i++) {
                    if (vinArr[i].vin === vin) {
                        alert("This VIN has already been entered!")
                        contains = true;
                        break;
                    }
                }

                if (vin === "") {
                    empty = true;
                    alert("Please add a vin!")
                }

                if (!empty && !contains) {

                    const newVehicle = {
                        vin: vin,
                        plateNumber: plateNumber,
                        insuranceExpiryDate: date,
                        color: color,
                        mileageInformation: [
                            {
                                lastUpdated: moment().format('L'),
                                lastMileage: 0,
                                oilChangeStartInterval: false
                            }
                        ],
                        warrantyInfo: []
                    }

                    vehicle
                        .decodeVIN(vin)
                        .then(detail => {
                            setPrimaryInfo(newVehicle)
                            setVehicleType(detail.Results[21].Value);
                            setMake(detail.Results[6].Value);
                            setModel(detail.Results[8].Value);
                            setModelYear(detail.Results[9].Value);
                        });
                }


            })

        clearFields();

    }

    const cancelRedirect = () => {
        //console.log("this is working")
        history.push("/")
    }

    return (
        <div>
            <VehicleDetailForm enterVehicleDetail={enterVehicleDetail} handleVin={handleVin} vin={vin} handlePlateNum={handlePlateNum} plateNumber={plateNumber} handleColor={handleColor} color={color} handleInsuranceDate={handleInsuranceDate} date={date} cancelRedirect={cancelRedirect} />
            <VehicleConfirmation vehicleType={vehicleType} primaryInfo={primaryInfo} make={make} model={model} modelYear={modelYear} />
        </div>
    )
}

export default VehicleStatus;