import React, { useEffect, useState } from 'react';
import Modal from 'react-modal';
import vehicle from '../services/vehicle'
import { useParams } from "react-router-dom"
import MileageTracker from '../components/MileageTracker'
import WarrantyTracker from '../components/WarrantyTracker'
import InsuranceExpiryTracker from '../components/InsuranceExpiryTracker'


Modal.setAppElement('#root');

const VehicleStatus = () => {
    const [vehicleInfo, setVehicleInfo] = useState([])

    let { id } = useParams();

    useEffect(() => {
        vehicle
            .getVehicle(id)
            .then(data => {
                //console.log(data)
                setVehicleInfo(data)
            })
    }, [])



    return (
        <div class="flex justify-evenly m-1">
            <div class="m-3 p-2 w-1/4 bg-gray-200 rounded-lg h-1/2">
                <h1 class="text-center text-4xl font-bold mb-4 ">Vehicle Details</h1>
                <div class="text-center space-y-2">
                    <div>
                        <p class="text-3xl font-bold">Year</p>
                        <p class="text-xl">{vehicleInfo.modelYear}</p>
                    </div>
                    <div>
                        <p class="text-3xl font-bold">Make</p>
                        <p class="text-xl">{vehicleInfo.make}</p>
                    </div>
                    <div>
                        <p class="text-3xl font-bold">Model</p>
                        <p class="text-xl">{vehicleInfo.model}</p>
                    </div>
                </div>
            </div>

            <div class="border-2 w-3/4 rounded-lg m-3 bg-gray-200 space-y-5">
                <InsuranceExpiryTracker data={vehicleInfo}/>
                <MileageTracker data={vehicleInfo} />
                <WarrantyTracker data={vehicleInfo} />
            </div>
        </div>
    )
}

export default VehicleStatus;