const DashboardQuickStats = ({ totalKilometers, totalVehicles }) => {

    return (
        <div>
            <div class="pt-6 border-2 rounded-lg p-2">
                <h1 class="text-center text-4xl">Welcome </h1>
                <p class="text-center pt-4 font-bold">These are the vehicles that you are currently tracking!</p>
                <div class="mt-3 border-t-2 border-black">
                    <p class="text-center font-black">Quick Stats:</p>
                    <div class="flex justify-center">
                        <p class="text-center pl-2 pr-2">Total Kilometers Travelled By All Vehicles:</p>
                        <p>{totalKilometers} km</p>
                    </div>
                    <div class="flex justify-center">
                        <p class="text-center pl-2 pr-2">Total # Of Vehicles:</p>
                        <p>{totalVehicles}</p>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default DashboardQuickStats;