import React, { useState, useEffect } from 'react';
import Modal from 'react-modal';
import moment from 'moment';
import vehicle from '../services/vehicle';
import { useParams } from "react-router-dom";
import TrackOilLife from '../components/TrackOilLife';


const MileageTracker = ({ data }) => {

    const [oilModal, setOilModal] = useState(false);
    const [mileageModal, setMileageModal] = useState(false);
    const [mileage, setMileage] = useState(0);
    const [lastMileage, setLastMileage] = useState("");
    const [lastUpdated, setLastUpdated] = useState("");
    const [oilChangeInterval, setOilChangeInterval] = useState("8000");
    const [mileageList, setMileageList] = useState([]);
    let { id } = useParams();

    useEffect(() => {
        vehicle
            .getVehicle(id)
            .then(response => {
                //console.log(response)
                setLastMileage(response.mileageInformation[response.mileageInformation.length - 1].lastMileage)
                setLastUpdated(response.mileageInformation[response.mileageInformation.length - 1].lastUpdated)
                setMileageList(response.mileageInformation)
            });
    }, []);

    const openMileageModal = () => {
        //console.log("working")
        setMileageModal(true);
    };

    const closeMileageModal = () => {
        setMileageModal(false);
        enterUpdateMiles(mileage);;
    };

    const openOilModal = () => {
        setOilModal(true);
    };

    const closeOilModal = () => {
        setOilModal(false);
        updateOilChange();
    };

    const handleOilChangeInterval = (e) => {
        setOilChangeInterval(e.target.value);
    };

    const handleMiles = (e) => {
        setMileage(e.target.value);
    };

    const updateOilChange = () => {
        if (Number(mileage) !== 0) {

            const newMileageObject = {
                lastUpdated: moment().format('L'),
                lastMileage: mileage,
                oilChangeStartInterval: true,
                oilChangeInterval: oilChangeInterval
            }

            data.mileageInformation.push(newMileageObject);
            //console.log(data);

            vehicle
                .updateVehicle(id, data)
                .then(response => {
                    //console.log(response)
                    setLastMileage(response.mileageInformation[response.mileageInformation.length - 1].lastMileage)
                    setLastUpdated(response.mileageInformation[response.mileageInformation.length - 1].lastUpdated)
                    setMileageList(response.mileageInformation)
                });
        }
    }

    const enterUpdateMiles = (miles) => {
        if (Number(miles) !== 0) {
            //console.log("working")
            const mileageUpdate = {
                lastUpdated: moment().format('L'),
                lastMileage: mileage,
                oilChangeStartInterval: false
            }

            let newList = { ...data }
            newList.mileageInformation.push(mileageUpdate)
            vehicle
                .updateVehicle(id, newList)
                .then(response => {
                    //console.log(response)
                    setLastMileage(response.mileageInformation[response.mileageInformation.length - 1].lastMileage)
                    setLastUpdated(response.mileageInformation[response.mileageInformation.length - 1].lastUpdated)
                    setMileageList(response.mileageInformation)
                })
            setMileage(0);
        }
    }



    return (
        <div>
            <div class="m-2 border-2 border-black rounded-lg">
                <h1 class="text-center text-4xl font-bold">Mileage Tracker</h1>
                <p class="text-center">Be sure to update your mileage frequently to generate the most accurate recommendations!</p>
                <div class="flex justify-evenly m-2">
                    <div class="w-1/2">
                        <div class="m-2 border-b-2 border-black space-y-2 p-2">
                            <p class="text-4xl">Current Mileage</p>
                            <p class="text-2xl">{lastMileage}</p>
                        </div>
                        <div class="m-2 space-y-2 p-2">
                            <p class="text-4xl">Last Updated</p>
                            <p class="text-2xl">{lastUpdated}</p>
                        </div>
                        <div class="flex justify-center m-2">
                            <button class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-full" onClick={openMileageModal}>Update Mileage</button>
                            <Modal
                                isOpen={mileageModal}
                                onRequestClose={closeMileageModal}
                                style={{
                                    overlay: {

                                    },
                                    content: {
                                        marginRight: "30%",
                                        marginLeft: "30%",
                                        marginTop: "10em",
                                        height: "250px"
                                    },
                                }}
                            >
                                <div class="m-2">
                                    <h1 class="text-center text-4xl mb-4">Update Mileage</h1>
                                    <div class="mb-5">
                                        <form class="grid-cols-1" onSubmit={enterUpdateMiles}>
                                            <div>
                                                <label>Current Mileage</label>
                                                <br></br>
                                                <input type="text" class="border-2 w-full" placeholder="0" onChange={handleMiles}></input>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                                <button onClick={closeMileageModal} class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-full">Update</button>

                            </Modal>
                        </div>
                    </div>
                    <div class="w-1/2 m-3">
                        <h1 class="text-center text-3xl font-bold">Mileage History</h1>
                        <div class="border-black border-l-2 overflow-y-scroll h-52">

                            {mileageList.map(p => {
                                return (
                                    <div class="border-b-2 border-black m-2 mb-3 flex justify-evenly w-1/2 container mx-auto" key={p.lastMileage}>
                                        <p class="text-xl">{p.lastUpdated}</p>
                                        <p class="text-xl">{p.lastMileage}km</p>
                                    </div>
                                )
                            })}

                        </div>
                    </div>
                </div>
            </div>

            <div class="m-3 mt-3 border-2 border-black rounded-lg p-2">
                <h1 class="text-center text-4xl font-bold">Oil Change Tracker</h1>
                <TrackOilLife mileageList={mileageList} oilChangeInterval={oilChangeInterval} />
                <div class="flex justify-center mt-2">
                    <button class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-full" onClick={openOilModal} >Update Oil Change</button>
                    <Modal
                        isOpen={oilModal}
                        onRequestClose={closeOilModal}
                        style={{
                            overlay: {

                            },
                            content: {
                                marginRight: "30%",
                                marginLeft: "30%",
                                marginTop: "10em",
                                height: "300px"
                            },
                        }}
                    >
                        <div class="m-2">
                            <h1 class="text-center text-4xl mb-4">Update Oil Change</h1>
                            <div class="mb-5">
                                <form class="grid-cols-1 space-y-7" >
                                    <div>
                                        <label>Current Mileage</label>
                                        <br></br>
                                        <input type="text" class="border-2 w-full" onChange={handleMiles}></input>
                                    </div>
                                    <div>
                                        <label>Oil Change Interval</label>
                                        <br></br>
                                        <select onChange={handleOilChangeInterval} class="border-2">
                                            <option value="8000">8000km</option>
                                            <option value="12000">12000km</option>
                                            <option value="16000">16000km</option>
                                        </select>
                                    </div>
                                </form>
                            </div>
                        </div>
                        <button class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-full" onClick={closeOilModal}>Update</button>

                    </Modal>
                </div>

            </div>

        </div>
    )
}

export default MileageTracker;