import './App.css';
import logo from "./resources/LogoMakr-4mt0AN.png"
import React, { useState } from 'react';
import {
  BrowserRouter as Router,
  Link
} from "react-router-dom";
import Routes from './Routes';


const App = () => {
  const [link, setLink] = useState("Add Vehicle");
  const [url, setUrl] = useState("/add-vehicle");

  const setPathName = (pathName) => {
    if (pathName === '/add-vehicle') {
      setLink("User Dashboard");
      setUrl("/user-dashboard");
    }

    if (pathName === '/user-dashboard') {
      setLink("Add Vehicle");
      setUrl("/add-vehicle");
    }

    if (pathName.slice(1, 15) === "vehicle-status") {
      setLink("User Dashboard");
      setUrl("/user-dashboard");
    }

    //console.log("**", pathName)
  }


  // add what kind of vehicle it is from api
  return (
    <Router>
      <div class="mx-auto lg:w-full ">

        <div class="text-white flex justify-between border-b-2 border-black h-28 pt-6 pr-4 pl-4">
          <img src={logo} class="h-14"></img>
          <div class="space-x-5 mr-4 pt-2">
            <Link to={url}>
              <button class="bg-yellow-600 p-2 active:bg-yellow-600 hover:bg-blue-500" >{link}</button>
            </Link>
            <button class="bg-yellow-600 p-2 active:bg-yellow-600 hover:bg-blue-500 line-through">Logout</button>
            <button class="bg-yellow-600 p-2 active:bg-yellow-600 hover:bg-blue-500 line-through">Dark Mode</button>
          </div>
        </div>

        <Routes setPathName={setPathName} />
      </div>
    </Router>
  )
}

export default App;




