import axios from 'axios'
const baseUrl = '/vehicle-info'

const decodeVIN = (vin) => {
    const request = axios.get(`https://vpic.nhtsa.dot.gov/api/vehicles/decodevin/${vin}?format=json`)
    return request.then(response => response.data)
}

const getAllVehicles = () => {
    const request = axios.get(baseUrl)
    return request.then(response => response.data)
}

const addVehicle = (obj) => {
    const request = axios.post(`${baseUrl}`, obj)
    return request.then(response => response.data)
}

const updateVehicle = (id, obj) => {
    const request = axios.put(`${baseUrl}/${id}`, obj)
    return request.then(response => response.data)
}

const getVehicle = (id) => {
    const request = axios.get(`${baseUrl}/${id}`)
    return request.then(response => response.data)
}

const deleteVehicle = (id) => {
    const request = axios.delete(`${baseUrl}/${id}`)
    return request.then(response => response.data)
}

export default { getAllVehicles, addVehicle, getVehicle, deleteVehicle, decodeVIN, updateVehicle }

