import React, { useEffect, useState } from 'react'
import Modal from 'react-modal';
import vehicle from '../services/vehicle';
import { useParams } from "react-router-dom"
import moment from 'moment';


const WarrantyTracker = ({ data }) => {
    const [warrantyModal, setWarrantyModal] = useState(false)
    const [title, setTitle] = useState("")
    const [warrantyProvider, setWarrantyProvider] = useState("")
    const [details, setDetails] = useState("")
    const [warrantyList, setWarrantyList] = useState([])
    let { id } = useParams();

    useEffect(() => {
        vehicle
            .getVehicle(id)
            .then(data => {
                //console.log(data)
                setWarrantyList(data.warrantyInfo)
            })
    }, [])

    const openWarrantyModal = () => {
        setWarrantyModal(true);
    }

    const closeWarrantyModal = () => {
        setWarrantyModal(false);
        submitWarrantyDetails()
    }

    const handleTitle = (e) => {
        setTitle(e.target.value)
    }

    const handleProvider = (e) => {
        setWarrantyProvider(e.target.value)
    }

    const handleDetails = (e) => {
        setDetails(e.target.value)
    }

    const submitWarrantyDetails = () => {

        if (title === "") {
            alert("Please enter warranty information!")
        } else {
            const newWarranty = {
                warrantyTitle: title,
                warrantyProvider: warrantyProvider,
                warrantyDetail: details,
                warrantyDate: moment().format('L')
            }

            data.warrantyInfo.push(newWarranty)
            vehicle
                .updateVehicle(id, data)
                .then(data => {
                    setWarrantyList(data.warrantyInfo)
                })
        }

    }

    return (
        <div class="m-3 mt-3 border-2 border-black rounded-lg">
            <h1 class="text-center text-4xl font-bold">Warranty Tracker</h1>
            <p class="text-center mt-1 text-xl">Store important warranty information here.</p>
            <div class="mt-3 h-52 p-2 overflow-y-scroll space-y-2">

                {warrantyList.map(p => {
                    return (
                        <div class="border-2 border-black rounded-lg p-2" key={p.warrantyTitle}>
                            <p class="text-2xl font-bold">{p.warrantyTitle}</p>
                            <div>
                                <div class="flex space-x-2">
                                    <p class="italic text-xl">{p.warrantyProvider}</p>
                                    <p class="text-xl">{p.warrantyDate}</p>
                                </div>
                                <p class="text-lg">{p.warrantyDetail}</p>
                            </div>
                        </div>
                    )
                })}

            </div>
            <div class="flex justify-center mt-2 mb-3">
                <button class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-3/4" onClick={openWarrantyModal}>Update Warranty</button>
                <Modal
                    isOpen={warrantyModal}
                    onRequestClose={closeWarrantyModal}
                    style={{
                        overlay: {

                        },
                        content: {
                            marginRight: "30%",
                            marginLeft: "30%",
                            marginTop: "10em",
                            height: "370px"
                        },
                    }}
                >
                    <div class="m-2">
                        <h1 class="text-center text-4xl mb-4">Update Warranty</h1>
                        <div class="mb-5">
                            <form class="grid-cols-1 space-y-5">
                                <div>
                                    <label>Warranty Title</label>
                                    <br></br>
                                    <input type="text" class="border-2 w-full" onChange={handleTitle}></input>
                                </div>
                                <div>
                                    <label>Warranty Provider</label>
                                    <br></br>
                                    <input type="text" class="border-2 w-full" onChange={handleProvider}></input>
                                </div>
                                <div>
                                    <label>Warranty Details</label>
                                    <br></br>
                                    <input type="text" class="border-2 w-full" onChange={handleDetails}></input>
                                </div>
                            </form>
                        </div>
                    </div>
                    <button onClick={closeWarrantyModal} class="bg-yellow-600 p-1 active:bg-yellow-600 hover:bg-blue-500 w-full">Update</button>

                </Modal>
            </div>
        </div>
    )
}

export default WarrantyTracker;